@import "../../app/app-variables";
@import "../../app/app";

div.drink-prefs {
    align-items: center;
    background-color: #ffa500;
    background-image: linear-gradient(225deg, rgba(53, 53, 53, 0.04) 0%, rgba(53, 53, 53, 0.04) 25%,rgba(189, 189, 189, 0.04) 25%, rgba(189, 189, 189, 0.04) 50%,rgba(106, 106, 106, 0.04) 50%, rgba(106, 106, 106, 0.04) 75%,rgba(11, 11, 11, 0.04) 75%, rgba(11, 11, 11, 0.04) 100%),linear-gradient(250deg, rgba(77, 77, 77, 0.04) 0%, rgba(77, 77, 77, 0.04) 25%,rgba(113, 113, 113, 0.04) 25%, rgba(113, 113, 113, 0.04) 50%,rgba(23, 23, 23, 0.04) 50%, rgba(23, 23, 23, 0.04) 75%,rgba(182, 182, 182, 0.04) 75%, rgba(182, 182, 182, 0.04) 100%),linear-gradient(209deg, rgba(169, 169, 169, 0.04) 0%, rgba(169, 169, 169, 0.04) 25%,rgba(164, 164, 164, 0.04) 25%, rgba(164, 164, 164, 0.04) 50%,rgba(32, 32, 32, 0.04) 50%, rgba(32, 32, 32, 0.04) 75%,rgba(34, 34, 34, 0.04) 75%, rgba(34, 34, 34, 0.04) 100%),linear-gradient(233deg, rgba(113, 113, 113, 0.04) 0%, rgba(113, 113, 113, 0.04) 25%,rgba(177, 177, 177, 0.04) 25%, rgba(177, 177, 177, 0.04) 50%,rgba(188, 188, 188, 0.04) 50%, rgba(188, 188, 188, 0.04) 75%,rgba(84, 84, 84, 0.04) 75%, rgba(84, 84, 84, 0.04) 100%),linear-gradient(210deg, rgba(57, 57, 57, 0.04) 0%, rgba(57, 57, 57, 0.04) 25%,rgba(227, 227, 227, 0.04) 25%, rgba(227, 227, 227, 0.04) 50%,rgba(14, 14, 14, 0.04) 50%, rgba(14, 14, 14, 0.04) 75%,rgba(26, 26, 26, 0.04) 75%, rgba(26, 26, 26, 0.04) 100%),linear-gradient(90deg, rgb(255,165,0),rgb(220,66,230));
    display: flex;
    height: $gr-ll * 100%;
    justify-content: center;
    text-align: center;
}

ul.ings {
    display: flex;
    flex-wrap: wrap;
    height: ($gr-h + $gr-lh) * 100%;
    justify-content: space-around;
    list-style: none;
}
