@import "../../app/app-variables";
@import "../../app/app";

ul.drinks-list::-webkit-scrollbar {
  scroll-behavior: smooth;
  display: none;
}

ul.drinks-list {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 100%;
  width: 100%;

  li.drinks-list-empty {
    align-items: center;
    background-image: $bg-d-img-gradient;
    color: $cl;
    display: flex;
    height: 100%;
    padding: 0 $gr-l * 100%;
    justify-content: center;
  }

  li.drinks-list {
    cursor: pointer;
    display: block;
    height: ($gr-lh + $gr-lll) * 100vh;
    flex-basis: auto;
    flex-grow: 4;
    max-height: $gr-h * 100%;
    position: relative;
    width: 100%;

    span.drinks-list-nr {
      align-items: center;
      border-radius: 50%;
      border-style: solid;
      border-width: $gr-lll * 1em;
      display: flex;
      height: 2em;
      justify-content: center;
      left: $gr-h * 1em;
      position: absolute;
      top: $gr-h * 1em;
      width: 2em;
    }
  }

  li.drinks-list:only-child {
    height: 100%;
    max-height: 100%;
  }

  li.drinks-list-selected {
    height: ($gr-l + $gr-llh) * 100vh;
  }

  div.drink {
    height: 100%;
    margin: 0 auto;
    position: relative;
  }

  li.drinks-list:nth-child(odd) {
    background-color: $bg-cd;
    background-image: $bg-d-img-gradient;
    color: $cl;
  }

  li.drinks-list:nth-child(even) {
    background-color: $bg-cl;
    color: $cd;
  }
}
