div.category-prefs {
    ul.drinks-list {
        div.drink {
            width: 100%;
        }

        li.drinks-list {
            span.drinks-list-nr {
                display: none;
            }
        }

        li.drinks-list:only-child {
            padding: ($gr-ll * 100%) 0;
        }
    }
}

div.category-top {
    ul.drinks-list {
        li.drinks-list {
            padding: 0 ($gr-ll * 100%) 0 ($gr-llh * 100%);

            span.drinks-list-nr {
                background-color: $cat-top-c;
                border: 0;
                color: $cd;
            }
        }
    }
}

div.category-random {
    background-color: $bg-cd;
    background-image: $bg-d-img-gradient;
    color: $cl;

    div.drink {
        height: $gr-h * 100%;
        left: 0;
        margin: 0 auto;
        position: relative;

        div.drink-icon {
            width: $gr-l * 100%;
        }

        ul.drink-ings {
            width: ($gr-l + $gr-llh) * 100%;
        }
    }
}

