@import "../../app/app-variables";
@import "../../app/app";

div.drinks-count {
    align-content: space-evenly;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    width: 100%;

    h3.drinks-count {
        position: relative;
        text-align: center;
        width: 100%;
    }

    p.drinks-count {
        align-items: center;
        border: $gr-ll * 1em solid $cl;
        border-radius: 50%;
        display: flex;
        height: 3em;
        justify-content: center;
        // margin: 0 30%;
        position: relative;
        width: 3em;
    }
}
