div.drinks-random {
    align-items: center;
    display: flex;
    height: $gr-l * 100%;
    justify-content: center;
    position: relative;

    button.drinks-random {
        align-items: center;
        display: flex;
        height: $gr-lh * 100%;
        justify-content: space-between;
