@import "../app/app-variables";
@import "../app/app";
@import "./keyboards";

$row-h: 17%;
$row-item-h: $gr-hhh * 100%;
$btn-fn-padding: 0 $gr-h * 1em;

div.digipad {
    align-content: space-evenly;
    background-image: $kbrd-bg-img;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    position: relative;

    div.digipad-row {
        align-items: center;
        display: flex;
        flex-wrap: nowrap;
        height: $row-h;
        justify-content: space-around;
        margin: 0 $gr-ll * 100%;
        position: relative;
        width: 100%;

        span.keyboard-btn,
        span.digipad-txt {
            align-items: center;
            border-radius: $gr-ll * 1em;
            display: flex;
            height: $row-item-h;
            justify-content: center;
            min-width: $gr-lh * 100%;
            overflow: hidden;
            white-space: nowrap;
        }
    }

    div.digipad-row-txt {
        margin: 0;

        span.digipad-txt {
            background-color: $bg-cl;
            margin: 0 1em;
            width: $gr-l * 100%;
        }

        span.keyboard-btn {
            padding: $btn-fn-padding;
        }
    }

    div.digipad-row-btm {
        justify-content: space-evenly;
        margin: 0;

        span.keyboard-btn:first-of-type {
            visibility: hidden;
        }

        span.keyboard-btn {
            width: $gr-lh * 100%;
            padding: $btn-fn-padding;
        }
    }
}
