@import "../../../app/app-variables";
@import "../../../app/app";
@import "../../_categories";

@keyframes tracking-in-expand {
    0% {
        letter-spacing: -0.5em;
        opacity: 0;
    }
    40% {
        opacity: $gr-l;
    }
    100% {
        opacity: $gr-h;
    }
}

@keyframes focus-in-expand {
    0% {
        letter-spacing: -0.5em;
        filter: blur(12px);
        opacity: 0;
    }
    100% {
        filter: blur(0px);
        opacity: $gr-l;
    }
}

aside.cat-aside-narrow,
aside.cat-aside-wide {
    display: block;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    overflow: hidden;

    div.category-icon {
        animation: tracking-in-expand 1.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
        height: $gr-ll * 100%;

        svg.category-icon {
            transform: scale($gr-hhl);
        }
    }

    h2.category-name {
        animation: tracking-in-expand 1.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
        align-items: center;
        display: flex;
        justify-content: center;
    }

    nav.cat-aside-narrow,
    nav.cat-aside-wide {
        background-color: turquoise;
        bottom: 0;
        height: $gr-ll * 100%;
        position: absolute;
        width: 100%;
    }

    div.cat-aside {
        color: $cl;
        display: flex;
        flex-wrap: wrap;
        height: $cat-aside-space-h;
        justify-content: center;
    }
}

