aside.cat-aside-wide {
    //   background-image: -webkit-linear-gradient(145deg, #013A6B 70%, #004E95 30%);
    background-image: -webkit-linear-gradient(135deg, $bg-cd 70%, rgba(7, 7, 7, 0.9) 30%);
    width: $cat-wide-w;

    h2.category-name {
        height: $gr-ll * 100%;
    }

    div.cat-aside-wide-spacer {
        height: $cat-aside-space-h;
    }

    div.cat-aside {
        div.drinks-count {
            height: $gr-h * 100%;
        }

        button {
            display: inline-block;
            height: $gr-llh * 100%;
            position: relative;
            width: $gr-hl * 100%;
        }
    }
}
